<template>
  <div class="content" v-if="groupList.length">
    <div class="title-row">
      <img src="//cdn09.ehaier.com/shunguang/H5/www/img/sgmobile/groupBooking/icon-title-booking.png"/>
      <span class="title">{{totalNum}}人正在拼团</span>
      <div class="more" v-if="groupList.length > 2" @click="showListModal(true)">
        <span>查看全部</span>
        <van-icon name="arrow" size=".28rem" color="#333333"/>
      </div>
    </div>
    <template v-for="(item, index) in groupList.slice(0,2)">
      <div class="group-item"
        :key="index"
        :style="{borderBottom: index === 0 ? '1px solid #f5f5f5' : 0}"
        @click="goGroup(item)">
        <template v-if="item.actorList && item.actorList.length">
          <img
            v-for="(member, i) in item.actorList"
            :key="i"
            :style="{zIndex: item.actorList.length - i, left: -(i * 0.33) + 'rem'}"
            :src="member.memberHead"/>
          <div class="name" :style="{left: -((item.actorList.length - 1) * 0.33) + 'rem', width: '2.3rem'}">{{item.actorList[0].memberName}}</div>
        </template>
        <div class="detail-row">
          <span class="top">还差<span style="color: #FF5252">{{item.leftNum}}人</span>拼成</span>
          <span class="bottom">剩余<count-down :time="item.endTime - item.sysTime" @finish="onTimeDown"/></span>
        </div>
        <div class="button"><span>去参团</span></div>
      </div>
    </template>
    <van-popup v-model="showModal" style=" border-radius: .24rem;"  position="center" get-container="#app-warp">
      <div class="modal-box">
        <div class="top-bar">
          <div class="title">
            <span>正在拼单</span>
            <img v-on:click="showListModal(false)" :src="`${$cdnImageUrl}/common/control-close.png`" alt="" />
          </div>
        </div>
        <div class="list">
          <div
            class="group-item"
            v-for="(item,index) in groupList"
            :key="index"
            @click="goGroup(item)"
            style="border: 0;">
              <!-- :style="{zIndex: 1, left: -(i * 0.33) + 'rem'}" -->
            <template v-if="item.actorList && item.actorList.length">
              <img
                :src="item.actorList[0].memberHead"/>
              <div class="name">{{item.actorList[0].memberName}}</div>
            </template>
            <div class="detail-row">
              <span class="top">还差<span style="color: #FF5252">{{item.leftNum}}人</span>拼成</span>
              <span class="bottom">剩余<count-down :time="item.endTime - item.sysTime" @finish="onTimeDown"/></span>
            </div>
            <div class="button"><span>去参团</span></div>
          </div>
        </div>
      </div>
    </van-popup>
  </div>
</template>
<script>
import { CountDown } from 'vant';
import { getGoodsGroupList } from '@/http/service/groupBooking'
export default {
  components: {
    CountDown,
  },
  props: ['productInfo', 'activityInfo'],
  data() {
    return {
      headerList: [1, 2, 3, 4, 5],
      timeDown: 500000,
      groupList: [],
      totalNum: 0,
      showModal: false,
    }
  },
  watch: {
    activityInfo(newVal) {
      this.getBookingList();
    }
  },
  mounted() {
    this.getBookingList();
  },
  methods: {
    onTimeDown(item, index) {
      this.getBookingList();
    },
    getBookingList() {
      const param = {
        activityId: this.activityInfo.activityId,
        productId: this.productInfo.productId,
        clustersNum: this.activityInfo.clustersNum,
      };
      getGoodsGroupList(param).then(res => {
        console.log(res);
        if (res.success) {
          this.groupList = res.data;
          this.totalNum = res.totalCount;
        }
      })
    },
    showListModal(isShow) {
      this.showModal = isShow
    },
    goGroup(group) {
      this.showListModal(false)
      if (group.owner) {
        this.$router.push(`/groupBookingDetail?orderSn=${group.actorList[0].orderSn}`);
      } else {
        this.$emit('chooseGroup', group.actorList[0].groupId)
      }
    }
  }
}
</script>
